import Message from 'element-react/dist/npm/es5/src/message';
import i18n from '../../../language';

export default (msg, option) => {

  let customClass;
  switch(option.type) {
    case 'success':
      customClass = 'custom-message-success';
      break;
    case 'error':
      customClass = 'custom-message-danger';
      break;
    default:
      customClass = 'custom-message-success';
  }

  const perOption = {
    message: i18n.t(msg),
    type: 'success',
    // duration: 0,
    customClass
  };

  Message({...perOption, ...option})
}
