/**
 * 计数器
 * 主要用于节点失败次数检测
 **/
const index = 0;

export default class Alertor {
  constructor({
    init = 0,
    name = `alertor-${index}`,
    threshold,
    alarm = () => void (0)
  }) {
    this.init = init;
    this.name = name;
    this.count = init;
    this.threshold = threshold;
    this.alarm = alarm;
  }

  add(num = 1) {
    this.count += num;

    if (this.count >= this.threshold) {
      this.alarm && this.alarm(this.count);
      this.set(this.init || 0)
    }
  };

  set(num) {
    this.count = num;
  }

  reset() {
    this.count = 0
  }
}
