import React, { Component } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { createPortal } from 'react-dom';
import { updateModal } from '../../actions/modal';
import RankList from '../MsgBox/RankList';
import './modal.scss'

class Modal extends Component {
  static defaultProps = {
    contentType: 'modal-from',
    open: false,
  };

  onClose = () => {
    this.props.updateModal({open: false});
  };

  render() {
    const { modal, children } = this.props;
    let content;
    switch (modal.type) {
      case 'rank-list':
        content = <RankList close={this.onClose} data={this.props.rankList} />;
        break;
      default: content = children;
    }
    return createPortal((
      <div className={`modal-wrapper ${modal.open && 'modal-wrapper-active'}`}>
        <div className={`modal-content-wrapper ${modal.open && 'modal-content-wrapper-active'}`}>
          { content }
        </div>
      </div>
    ), document.getElementById('root'))
  }
}

function mapStateToProps(state) {
  return {
    language: state.language,
    modal: state.modal,
    rankList: state.rankInfo.rankList,
  };
}

function mapDispatchToProps(dispatch) {
  return bindActionCreators({updateModal}, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(Modal);
