import React, { Component } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import Button from 'element-react/dist/npm/es5/src/button';
import Layout from 'element-react/dist/npm/es5/src/layout';
import Message from '../Parts/Message/Message';
import Player from '../Parts/Player/Player';
import { contractAction } from '../../actions';
import { getKeyProfit, getEstimateProfit } from '../../Utils/getProfit';
import { min2EOS, min2key } from '../../Utils/unitConvert';
import i18n from '../../language';
import './Tabs.scss';


class Me extends Component {
  state = {
    isFetchingDrawOut: false
  };

  withdraw = () => {
    if (!this.props.identity) {
      Message('not_login', { type: 'error' });
      return;
    }

    this.setState({ isFetchingDrawOut: true });
    this.props.contractAction(this.props.contract[0], 'withdraw', {
      user: this.props.identity.name,
    })
      .then(res => {
        Message('withdraw_success', { type: 'success' });
        this.setState({ isFetchingDrawOut: false });
      })
      .catch(err => {
        console.log(err, 'withdraw_fail');
        Message('withdraw_fail', { type: 'error' });
        this.setState({ isFetchingDrawOut: false });
      });
  };

  render() {
    const { myBonus, playerKeys = 0, mask: playerMask, lockMask: playerMaskLocked, historyBalance, upstreamPlayer } = this.props.playerInfo;
    const { round, roundKeys, eos, mask: roundMask, lockMask: roundMaskLocked, baseValue, rootPlayer } = this.props.roundInfo;
    const { downstreamList = [], isUnlocked, lockValue: affiliateLockValue, unlockedQuantity, lockedValue } = this.props.affiliateInfo;

    const keyProfit = getKeyProfit(roundMask, playerMask, playerKeys, baseValue);
    const keyProfitLocked = getKeyProfit(roundMaskLocked, playerMaskLocked, playerKeys, baseValue);

    const affProfit = void (0) || 0;
    const affProfitLocked = void (0) || 0;

    const totalProfit = min2EOS(keyProfit + affProfit + historyBalance); //
    const totalAffProfitLocked = min2EOS(keyProfitLocked + affProfitLocked);

    const isBindUpstream = upstreamPlayer && upstreamPlayer !== rootPlayer; // 是否绑定

    // 开奖后奖励
    const estimateProfit = getEstimateProfit(eos, unlockedQuantity, isUnlocked);
    return (
      <div className="tabs-wrapper">
        <div className="tabs-me-card-content-wrapper tabs-bottom-gap">
          <div className="tabs-me-text-summary tabs-bottom-gap">
            <img className="tabs-me-img-key" src={require("../../images/key.png")} />
            <span>持有的Key：</span>
            <span>{min2key(playerKeys)}</span>
          </div>
          <div className="line-light-m tabs-small-bottom-gap" />
          <div className="tabs-me-text-summary-1">
            <p className="text-normal-light align-center">累计分红</p>
            <span>{parseFloat(totalProfit.toFixed(4)) || 0} EOS</span> | <span className="text-tiny-light-s">锁定</span> <span className="title-small-light-s">{parseFloat(totalAffProfitLocked.toFixed(4)) || 0}</span>
          </div>
        </div>

        <div className="tabs-me-card-content-wrapper tabs-bottom-gap">
          {
            myBonus != undefined && myBonus !== 0 &&
            <Layout.Row className="tabs-small-bottom-gap">
              <Layout.Col span="12">
                <p className="text-medium-light">我的中奖</p>
              </Layout.Col>
              <Layout.Col className="align-right" span="12">
                <p className="text-medium-light align-right">{min2EOS(myBonus) || 0} EOS</p>
              </Layout.Col>
            </Layout.Row>
          }
          <Layout.Row className="tabs-small-bottom-gap">
            <Layout.Col span="12">
              <p className="text-medium-light">本轮key分红</p>
            </Layout.Col>
            <Layout.Col className="align-right" span="12">
              <p className="text-medium-light align-right">{min2EOS(keyProfit) || 0} EOS</p>
            </Layout.Col>
          </Layout.Row>
          <p className="text-medium-light align-right tabs-small-bottom-gap">锁定：{min2EOS(keyProfitLocked) || 0}</p>
          <Layout.Row className="tabs-small-bottom-gap">
            <Layout.Col span="12">
              <p className="text-medium-light">历史未提取</p>
            </Layout.Col>
            <Layout.Col className="align-right" span="12">
              <p className="text-medium-light align-right">{min2EOS(historyBalance) || 0} EOS</p>
            </Layout.Col>
          </Layout.Row>
        </div>

        <div className="tabs-me-card-content-wrapper">
          <Layout.Row className="">
            <Layout.Col span="12">
              <p className="text-medium-light">开奖后分红（预估）</p>
            </Layout.Col>
            <Layout.Col className="align-right" span="12">
              <span className="title-small-light">{ parseFloat(estimateProfit.toFixed(4)) || 0 } EOS</span>
            </Layout.Col>
          </Layout.Row>
        </div>

        <Button className="tabs-btn-join-link" type="text">
          推荐好友
        </Button>
        <p className="tabs-me-text-subordinates">我的推荐人：{upstreamPlayer}</p>
        <div className="tabs-me-card-subordinates-wrapper">
          <Layout.Row className="">
            <Layout.Col span="12">
              <p className="text-medium-light">直推奖励</p>
            </Layout.Col>
            <Layout.Col className="align-right" span="12">
              <span className="title-small-light">{min2EOS(lockedValue) || 0} EOS</span>
            </Layout.Col>
          </Layout.Row>
          <p className="text-tiny-light tabs-bottom-gap">{isUnlocked ? '奖励提取条件已达成' : '奖励提取条件未达成'}</p>
          <Layout.Row className="tabs-small-bottom-gap">
            <Layout.Col span="12">
              <p className="title-small-light">我推荐的玩家</p>
            </Layout.Col>
            <Layout.Col className="align-right" span="12">
              <span className="title-medium-light">{downstreamList.length}</span>
            </Layout.Col>
          </Layout.Row>
          <div className="line-light-m tabs-bottom-gap" />

          <div className="tabs-me-subordinates-list-wrapper">
            {
              downstreamList
                .map((d, i) => <Player key={i + 'subordinates'} name={d.name} invest={d.keys} commission={`+${min2EOS(d.profit)} EOS`} />)
            }
          </div>
        </div>
        <Button
          className="tabs-btn-withdraw-dividends"
          type="info"
          loading={this.state.isFetchingDrawOut}
          onClick={this.withdraw}
        >
          取出分红
        </Button>
      </div>
    )
  }
}

function mapStateToProps(state) {
  return {
    language: state.language,
    contract: state.contract,
    roundInfo: state.roundInfo || {},
    playerInfo: state.playerInfo || {},
    affiliateInfo: state.affiliateInfo,
    identity: state.identity
  };
}

function mapDispatchToProps(dispatch) {
  return bindActionCreators({contractAction}, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(Me);
