/**
 * 红利计算
 **/
import BigNumber from 'bignumber.js';

// key红利
export function getKeyProfit(roundMask, playerMask, playerKeys, base) {
  // rnd.mask * p.prd / base - p.mask
  return new BigNumber(roundMask).div(base).times(playerKeys).minus(playerMask).toNumber()
}

// token红利
export function getTokenProfit(tokenAmount, tokenBalance, payoutsTo, base) {
  // 分红值 / 1000w * balance - payoutsTo_
  return new BigNumber(tokenAmount).div(10000).div(base).times(tokenBalance).minus(payoutsTo).toNumber()
}

// 开奖后达成红利
export function getEstimateProfit(eos, unlockedQuantity, isUnlocked) {
  if (!isUnlocked || !parseFloat(unlockedQuantity)) return 0;
  return new BigNumber(eos).div(10000).times(0.15).div(unlockedQuantity).toNumber()
}

// 游戏总分红
export function getGameProfit(eos) {
  return eos * 0.55;
}
