/**
 * 轮询查表
 **/

import EOS from "eosjs";
import BigNumber from "bignumber.js";
import _compact from "lodash/compact";
import _filter from "lodash/filter";
import { UPDATE_ROUND_INFO, UPDATE_TOKEN_INFO, UPDATE_PLAYER_INFO, UPDATE_AFFILIATE_INFO } from "./index";
import { toggleNetwork, getReadOnlyEOS, getEOSClient } from "../networks/index";
import Alertor from "../Utils/Alertor";

export const UPDATE_REMOTE_TIME = Symbol('UPDATE_REMOTE_TIME');
export const UPDATE_TIME_LEFT = Symbol('UPDATE_TIME_LEFT');
export const UPDATE_RANK_INFO = Symbol('UPDATE_RANK_INFO');

// 校对时间
export function getRemoteTime() {
  return (dispatch, getState) => {
    // 模拟获取服务器时间
    Promise.resolve(Date.now() - parseInt(Math.random()* 5000 - 2500))
      .then((r) => {
        const timeOffset = r - Date.now();
        dispatch({ type: UPDATE_REMOTE_TIME, payload: { timeOffset } });
        console.log(timeOffset, '11111-------')
      })
  }
}

// 计算剩余时间 (获取到round信息后)
export function startCount() {
  return (dispatch, getState) => {
    timeInspector(1000, false, () => {
      const { roundInfo = {}, timeOffset } = getState();
      if (roundInfo.startTime && roundInfo.startTime * 1000 > Date.now()) {
        console.log('中场阶段');
        const timeLeft = roundInfo.startTime * 1000 - Date.now() + timeOffset;
        dispatch({ type: UPDATE_ROUND_INFO, payload: { status: 'pause', timeLeft } });
        return;
      }

      if (roundInfo.endTime && roundInfo.endTime * 1000 < Date.now()) {
        console.log('待开奖阶段');
        dispatch({ type: UPDATE_ROUND_INFO, payload: { status: 'pause', timeLeft: 0 } });
        return;
      }

      if (roundInfo.endTime) {
        const timeLeft = roundInfo.endTime * 1000 - Date.now() + timeOffset;
        dispatch({ type: UPDATE_ROUND_INFO, payload: { status: 'running', timeLeft } });
      }
    })();
  }
}

let timerWidthID = {};

// 获取常量TTC总发行量
export function getTokenSupply() {
  return (dispatch, getState) => {
    const {contract, tokenInfo} = getState();
    getReadOnlyEOS().getTableRows({
      code: contract[1],
      json: true,
      scope: tokenInfo.symbol,
      table: tokenInfo.table,
    })
      .then((res) => {
        console.log(res, 'getTokenSupply');
        const {rows = []} = res || {};
        const { max_supply } = rows[0] || {};
        dispatch({
          type: UPDATE_TOKEN_INFO,
          payload: { maxSupply: parseFloat(max_supply) }
        });
      })
      .catch(err => {
        console.warn(err);
      });
  };
}


// 无需身份的tableRow轮询
export function getTableRows() {
  const alertor = new Alertor({
    name: 'fetchAlertor',
    threshold: 3,
    alarm: () => toggleNetwork()
  });

  return (dispatch, getState) => {
    const { contract } = getState();

    // ROUND表
    timeInspector(3000, false, () => {
      getReadOnlyEOS().getTableRows({
        code: contract[0],
        json: true,
        scope: contract[0],
        table: 'round',
      })
        .then((res) => {
          console.log(res, 'round');
          const { rows = [] } = res || {};
          const { end: endTime, start: startTime, rid: round, eos, total, pot, prd: roundKeys, mask, lock_mask: lockMask, prd: currentKeys } = rows[0] || {};
          dispatch({
            type: UPDATE_ROUND_INFO,
            payload: {
              endTime,
              startTime,
              round,
              eos, // 总收到eos
              total, // 历史累计的eos
              pot, // 奖金
              roundKeys,
              mask,
              lockMask,
              currentKeys
            }
          });
        })
        .catch(err => {
          alertor.add();
          console.warn(err);
        });
    })();

    // RANK表
    timeInspector(1000 * 5, false, () => {
      getReadOnlyEOS().getTableRows({
        code: contract[0],
        json: true,
        scope: contract[0],
        table: 'history',
      })
        .then((res) => {
          console.log(res, 'history');
          const { rows = [] } = res || {};
          const { his = [] } = rows[0] || {};
          dispatch({
            type: UPDATE_RANK_INFO,
            payload: {
              rankList: his.reverse()
            }
          });
        })
        .catch(err => {
          alertor.add();
          console.warn(err);
        });
    })();
  }
}

// 登陆后获取身份的轮询
export function getTableRowsWidthId() {
  const alertor = new Alertor({
    name: 'fetchAlertor',
    threshold: 5,
    alarm: () => toggleNetwork()
  });

  return (dispatch, getState) => {

    const { contract, identity = {} } = getState();

    // PLAYERS表
    timeInspector(5000, 'aboutPlays', () => {
      getReadOnlyEOS().getTableRows({
        code: contract[0],
        json: true,
        scope: contract[0],
        table: 'players',
      })
        .then((res) => {
          console.log(res, 'playersssss');
        });

      getReadOnlyEOS().getTableRows({
        code: contract[0],
        json: true,
        scope: contract[0],
        table: 'players',
        table_key: '1',
        key_type: 'i64',
        lower_bound: new BigNumber(EOS.modules.format.encodeName(identity.name, false)).toString(),
        upper_bound: new BigNumber(EOS.modules.format.encodeName(identity.name, false)).toString(),
      })
        .then((res) => {
          const { rows = [] } = res || {};
          const { pot_vault: myBonus, prd: playerKeys, eos, lock_mask: lockMask, mask, balance: historyBalance, aff: upstreamPlayer } = rows[0] || {};

          console.log(res, identity.name, 'players');
          dispatch({
            type: UPDATE_PLAYER_INFO,
            payload: {
              fetchStatus: !!res.rows, // 判断本次数据有没有取回来
              playerKeys,
              eos,
              lockMask,
              mask,
              historyBalance,
              upstreamPlayer,
              myBonus
            }
          });
        })
        .catch(err => {
          alertor.add();
          console.warn(err);
        });

      // TTC分红表 ico合约
      getReadOnlyEOS().getTableRows({
        json: true,
        code: contract[1],
        scope: identity.name, //'prdttestoooo', //
        table: 'accounts',
      })
        .then((res) => {
          console.log(res, 'accounts');
          const { rows = [] } = res || {};
          const { payoutsTo_: payoutsTo, balance } = rows[0] || {};
          dispatch({
            type: UPDATE_TOKEN_INFO,
            payload: { payoutsTo, balance }
          });
        })
        .catch(err => {
          alertor.add();
          console.warn(err);
        });

      // TTC分红基数
      getReadOnlyEOS().getTableRows({
        code: contract[0],
        json: true,
        scope: contract[0],
        table: 'share',
      })
        .then((res) => {
          const { rows = [] } = res || {};
          const { amount: tokenAmount } = rows[0] || {};
          console.log(res, 'share');
          dispatch({
            type: UPDATE_ROUND_INFO,
            payload: { tokenAmount }
          });
        })
        .catch(err => {
          alertor.add();
          console.warn(err);
        });

      // 所有推荐人表 计算开奖后分红
      getReadOnlyEOS().getTableRows({
        code: contract[0],
        json: true,
        scope: contract[0],
        table: 'affiliate',
      })
        .then((res) => {
          const { rows = [] } = res || {};
          console.log(res, 'all affiliate');

          let unlockedQuantity = 0;
          rows.forEach(r => {
            if (isUnlocked(r.id)) {
              unlockedQuantity++;
            }
          });

          dispatch({
            type: UPDATE_AFFILIATE_INFO,
            payload: { unlockedQuantity }
          });
        })
        .catch(err => {
          alertor.add();
          console.warn(err);
        });

      // 玩家推荐人表
      getReadOnlyEOS().getTableRows({
        code: contract[0],
        json: true,
        scope: contract[0],
        table: 'affiliate',
        table_key: '1',
        key_type: 'i64',
        lower_bound: new BigNumber(EOS.modules.format.encodeName(identity.name, false)).toString(),
        upper_bound: new BigNumber(EOS.modules.format.encodeName(identity.name, false)).toString(),
      })
        .then((res) => {
          console.log(res, 'affiliate');
          const { rows = [] } = res || {};
          const { id: downstreamList = [], locked_value: lockedValue } = rows[0] || {};

          const downstreamListNew = _compact(
            downstreamList.map((d) => {
              if (d.key !== '') {
                return {
                  name:d.key,
                  keys: d.value.field_0,
                  profit: d.value.field_1
                }
              }
            })
          );

          dispatch({
            type: UPDATE_AFFILIATE_INFO,
            payload: { lockedValue, downstreamList: _compact(downstreamListNew), isUnlocked: isUnlocked(downstreamList) }
          });
        })
        .catch(err => {
          alertor.add();
          console.warn(err);
        });
    })();

    console.log('timerWidthID', timerWidthID)
  }
}

// 停止轮询个人信息
export function stopGetTableRowsWithId () {
  if (timerWidthID) {
    console.log(timerWidthID, 'timerWidthID');
    for (let i in timerWidthID) {
      window.cancelAnimationFrame(timerWidthID[i])
    }
  }
}

// 间隔检查器
function timeInspector(time, key, callback) {
  let lastTime = 0;
  return function iterate() {
    if (Date.now() - lastTime >= time) {
      lastTime = Date.now();
      callback()
    }
    const id = requestAnimationFrame(() => iterate());

    // 保存最新的id
    if (key) {
      timerWidthID[key] = id;
    }
  }
}

// 是否解锁
function isUnlocked(list) {
  if (list.length < 2) {
    return false;
  }
  return _filter(list, function(d) {return d.value.field_0 >= 500 * 10}).length >= 2
}
