import {
  ACTION_TEST,
  UPDATE_IDENTITY,
  CHANGE_LANGUAGE,
  UPDATE_ROUND_INFO,
  UPDATE_TOKEN_INFO,
  UPDATE_PLAYER_INFO,
  CLEAR_PLAYER_INFO,
  UPDATE_AFFILIATE_INFO,
  UPDATE_WALLET_INFO
} from '../actions';
import {UPDATE_REMOTE_TIME, UPDATE_TIME_LEFT, UPDATE_RANK_INFO} from '../actions/time';
import {UPDATE_MODAL} from '../actions/modal';
import i18n from '../language';

export const initialState = {
  i18n,
  language: void (0),
  socialContact: 'https://t.me/joinchat/Esi1OkPktgcFeJ3Lmlcrqp',
  contract: ['prdcontract2', 'prdttestiiii'],
  roundInfo: {
    fetchStatus: void (0),
    status: void (0), // [pause, running]
    baseValue: 1000 * 1000,
    endTime: void (0),
    startTime: void (0),
    timeLeft: void (0),
    round: void (0),
    eos: void (0),
    total: void (0), // 历史投资的eos
    pot: void (0), // 奖池
    roundKeys: void (0),
    mask: void (0),
    lockMask: void (0),
    tokenAmount: void (0), // TTC分红基数
    rootPlayer: 'seektheworld', // 官方玩家名
    minInvestment: 1, // 最小投资key
  },
  playerInfo: {
    fetchStatus: void (0),
    playerKeys: void (0),
    mask: void (0),
    lockMask: void (0),
    historyBalance: void (0), // 历史分红
    upstreamPlayer: void (0), // 上家
    myBonus: void (0), // 我的大奖分红
  },
  tokenInfo: {
    fetchStatus: void (0),
    balance: void (0), // token余额
    payoutsTo: void (0), // token支出
    symbol: 'TTC',
    table: 'stat',
    maxSupply: 10000000
  },
  affiliateInfo: {
    fetchStatus: void (0),
    isUnlocked: false,
    downstreamList: [],
    lockValue: void (0),
    unlockedQuantity: void (0)
  },
  rankInfo: {
    // latestIndex: 0, // 历史交易增量点
    fetchStatus: void (0),
    rankList: [], //rid: 1, winner: "lcbsss123455", amount: 39, rank: Array(0)
  },
  identity: null,
  timeOffset: 0, // 服务器时间校正
  modal: {
    open: false,
    types: ['rank-list'],
    type: void (0),
    data: void (0)
  },
  // 钱包调用信息
  walletInfo: {
    actionName: void (0),
    isWalletOccupied: false
  },
};

export default function (state = initialState, action) {
  switch (action.type) {
    case ACTION_TEST:
      return {...state, data: action.payload.data};
    case CHANGE_LANGUAGE:
      return {...state, language: action.payload.language};
    case UPDATE_IDENTITY:
      return {...state, identity: action.payload.identity};
    case UPDATE_REMOTE_TIME:
      return {...state, timeOffset: action.payload.timeOffset};
    case UPDATE_TIME_LEFT:
      return {...state, timeLeft: action.payload.timeLeft};
    case UPDATE_ROUND_INFO:
      return {...state, roundInfo: {...state.roundInfo, ...action.payload}};
    case UPDATE_TOKEN_INFO:
      return {...state, tokenInfo: {...state.tokenInfo, ...action.payload}};
    case UPDATE_PLAYER_INFO:
      return {...state, playerInfo: {...state.playerInfo, ...action.payload}};
    case CLEAR_PLAYER_INFO:
      return {...state, playerInfo: {...initialState.playerInfo}};
    case UPDATE_AFFILIATE_INFO:
      return {...state, affiliateInfo: {...state.affiliateInfo, ...action.payload}};
    case UPDATE_RANK_INFO:
      return {...state, rankInfo: {...state.rankInfo, ...action.payload}};
    case UPDATE_MODAL:
      return {...state, modal: {...state.modal, ...action.payload}};
    case UPDATE_WALLET_INFO:
      return {...state, walletInfo: {...state.walletInfo, ...action.payload}};
    default:
      return state;
  }
};
