/**
 * 输出当前
 * -network
 * -EOSClient
 * -readOnlyEOS
 * 的单例
 **/
import EOS  from "eosjs";
import networks from "./networks";
import ScatterJS from "scatterjs-core";

let networkIndex = 0;

let EOSClient;

let readOnlyEOS = EOS({
  chainId: networks[networkIndex].chainId,
  httpEndpoint: networks[networkIndex].protocol + "://" + networks[networkIndex].host + ":" + networks[networkIndex].port
});

export const toggleNetwork = () => {
  networkIndex = networkIndex + 1 < networks.length ? networkIndex + 1 : 0;

  readOnlyEOS = EOS({
    chainId: networks[networkIndex].chainId,
    httpEndpoint: networks[networkIndex].protocol + "://" + networks[networkIndex].host + ":" + networks[networkIndex].port
  });

  EOSClient = ScatterJS.scatter.eos(networks[networkIndex], EOS);
  console.log('节点切换到:', networkIndex)
};

export const getNetwork = () => networks[networkIndex % networks.length];

export const getReadOnlyEOS = () => readOnlyEOS;

export const initEOSClient = () => {
  EOSClient = ScatterJS.scatter.eos(networks[networkIndex], EOS);
  return EOSClient;
};

export const getEOSClient = () => EOSClient;

export const clearEOSClient = () => {
  EOSClient = null;
};
