/**
 * 单局top排行
 **/
import React, {Component} from 'react';
import Layout from 'element-react/dist/npm/es5/src/layout';
import {bindActionCreators} from 'redux';
import {connect} from 'react-redux';
import {transfer} from '../../actions';
import { min2EOS } from '../../Utils/unitConvert';
import './MsgBox.scss';
import i18n from '../../language';


class RoundRankList extends Component {
  render () {
    const { rank = [], rid } = this.props.rankList[this.props.index] || [];
    return (
      <div className="modal-content-round-rank">
        <i className="el-icon-close modal-btn-close" onClick={this.props.close} />
        <p className="title-medium-light align-left">第{rid}轮</p>
        <p className="modal-round-rank-text-profit-top">分红排行榜</p>
        <div className="line-light-m" />
        <div className="round-rank-list-wrapper">
          {
            rank.map((d, i) => {
              return (
                <Layout.Row key={`round-rank-list-${i}`} className="round-rank-item-wrapper">
                  <Layout.Col span={4} className="text-normal-light align-left">
                    {
                      i <= 2 ?
                        <img className="modal-rank-img-medal" src={require(`../../images/No${i+1}.png`)} alt="" /> :
                        i + 1
                    }
                  </Layout.Col>
                  <Layout.Col span={8} className="text-normal-light">{d.first}</Layout.Col>
                  <Layout.Col span={12} className="text-normal-light align-right">{min2EOS(d.second)} EOS</Layout.Col>
                </Layout.Row>
              )
            })
          }
        </div>

      </div>
    )
  }
}

function mapStateToProps(state) {
  return {
    language: state.language,
    rankInfo: state.rankInfo,
    rankList: state.rankInfo.rankList || [],
    index: state.modal.data && state.modal.data.index
  };
}

function mapDispatchToProps(dispatch) {
  return bindActionCreators({transfer}, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(RoundRankList);

