export const ACTION_TEST = Symbol('ACTION_TEST');
export const UPDATE_IDENTITY = Symbol('UPDATE_IDENTITY');
export const UPDATE_ROUND_INFO = Symbol('UPDATE_ROUND_INFO');
export const UPDATE_TOKEN_INFO = Symbol('UPDATE_TOKEN_INFO');
export const UPDATE_PLAYER_INFO = Symbol('UPDATE_PLAYER_INFO');
export const CLEAR_PLAYER_INFO = Symbol('CLEAR_PLAYER_INFO');
export const UPDATE_AFFILIATE_INFO = Symbol('UPDATE_AFFILIATE_INFO');
export const CHANGE_LANGUAGE = Symbol('CHANGE_LANGUAGE');
export const UPDATE_WALLET_INFO = Symbol('UPDATE_WALLET_INFO');

ScatterJS.plugins(new ScatterEOS());

let isConnected;

// 自动登陆
export function initApp() {
  return async (dispatch, getState) => {
    const { language } = getState();
    // 注册自动登录
    document.addEventListener('scatterLoaded', () => {
      connect()(dispatch, getState);
    });

    // 设置本地语言
    i18n.changeLanguage(language, () => {
      // changeLanguage(language)(dispatch, getState);
    });

    // 获取TTC总量
    getTokenSupply()(dispatch, getState);

    // 同步remote时间
    // getRemoteTime()(dispatch, getState);

    // 获取游戏round信息
    getTableRows()(dispatch, getState);

    // 计算剩余时间
    startCount()(dispatch, getState);
  }
}

// 更新语言
export function changeLanguage(language) {
  return async (dispatch) => {
    i18n.changeLanguage(language);
    return dispatch({ type: CHANGE_LANGUAGE, payload: { language } })
  }
}

// 手动登陆
export function login() {
  return function (dispatch, getState) {
    console.log(isConnected, 'EOSClient');
    if (!isConnected) {
      connect()(dispatch);
    } else {
      initEOSClient();
      getId()(dispatch, getState);
    }
  }
}

// 登出
export function logout() {
  return function (dispatch) {
    ScatterJS.logout()
      .then((res) => {
        console.log(res, '登出成功');
        dispatch({ type: UPDATE_IDENTITY, payload: { identity: null } });
        clearEOSClient();

        // 推出登陆信息后停止轮询
        stopGetTableRowsWithId();

        // 清空play个人信息
        dispatch({ type: CLEAR_PLAYER_INFO })
      })
      .catch(err => {
        Message('logout_fail', { type: 'error' });
        console.log('登出失败')
      })
  }
}

// 获取身份
function getId() {
  return (dispatch, getState) => {
    ScatterJS.scatter && ScatterJS.scatter.getIdentity({ accounts: [getNetwork()] })
      .then(() => {
        // 获取身份成功
        const identity = ScatterJS.account('eos');
        console.log(identity, 'this.scatter.identity.accounts');
        dispatch({ type: UPDATE_IDENTITY, payload: { identity } });
        Message('login_success', { type: 'success' });

        // 获取登陆信息后开始轮询
        getTableRowsWidthId()(dispatch, getState);

        // contractAction('prdttesticco', 'withdraw', 'jungletestco')(dispatch, getState)
        //   .then(res => console.log(res, 99999999));
      })
      .catch((err) => {
        console.log(err, 'login_fail');
        Message('login_fail', { type: 'error' });
      });
  }
}

