// 合约操作0xa4992afa677abe87d20f559d61e8558d478f6b20
export function transfer({
  contract = 'eosio.token',
  actionName = 'transfer',
  to,
  quantity = '',
  memo = ''
}) {
  return (dispatch, getState) => {
    const { identity = {} } = getState();
    if (!identity) {
      return Promise.reject('未登陆账户');
    }
    // transaction方式
    return getEOSClient().transaction(
      {
        // ...headers,
        actions: [
          {
            account: contract,
            name: actionName,
            authorization: [{
              actor: identity.name,
              permission: identity.authority
            }],
            data: {
              from: identity.name,
              to: to,
              quantity,
              memo
            }
          }
        ]
      }
    )
      // .then(res => {console.log(res, 'fasong token')})
      .catch(err => {
        return Promise.reject(err);
      });
  }
}

// 合约操作
export function contractAction(contractName, actionName = 'withdraw', payload) {
  return (dispatch, getState) => {
    const { identity = {} } = getState();
    if (!identity) {
      return Promise.reject('未登陆账户');
    }

    return getEOSClient()
      .contract(contractName)
      .then((res) => {
        console.log(res, payload, 'payloadpayload')
        return res[actionName](payload, {
          authorization: [`${identity.name}@${identity.authority}`]
        })
          .then(r => {
            console.log(r, 'withdraw token rrrr')
          })
          .catch(err => {
            console.log(err, 'withdraw token errr');
            return Promise.reject(err);
          });
      })
      .then(res => {console.log(res, 'contractAction token')})
      .catch(err => {
        return Promise.reject(err);
      });
  }
}

// 钱包调取占用
export function updateWalletInfo(actionName, isWalletOccupied) {
  return (dispatch) => {
    dispatch({
      type: UPDATE_WALLET_INFO,
      payload: { actionName, isWalletOccupied }
    })
  }
}

