import React, { Component } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import Button from 'element-react/dist/npm/es5/src/button';
import Layout from 'element-react/dist/npm/es5/src/layout';
import Rank from './Rank';
import { min2EOS, min2key } from '../../Utils/unitConvert';
import { getKeyProfit, getGameProfit } from "../../Utils/getProfit";
import i18n from "../../language";
import './Tabs.scss'

class GameInfo extends Component {
  render() {
    const { round, roundKeys, eos, total, mask: roundMask, lockMask: roundMaskLocked, baseValue } = this.props.roundInfo;
    const { mask: playerMask, lockMask: playerMaskLocked, playerKeys, historyBalance } = this.props.playerInfo;

    const gameProfit = getGameProfit(eos);

    const keyProfit = getKeyProfit(roundMask, playerMask, playerKeys, baseValue);
    const keyProfitLocked = getKeyProfit(roundMaskLocked, playerMaskLocked, playerKeys, baseValue);

    const affProfit = void (0) || 0;
    const affProfitLocked = void (0) || 0;

    const myProfit = keyProfit + keyProfitLocked + affProfit + affProfitLocked + historyBalance;

    return [
      <div key="game-info-part-1" className="tabs-wrapper tabs-bottom-gap">
        <Layout.Row className="tabs-row-round">
          <Layout.Col span="12">
            <p className="title-medium-light">ROUND # {round}</p>
          </Layout.Col>
          <Layout.Col className="align-right" span="12">
            <span className="text-normal-light">{min2key(roundKeys) || 0}</span>
          </Layout.Col>
        </Layout.Row>
        <div className="tabs-game-card-content-wrapper">
          <Layout.Row className="tabs-basic-bottom-gap">
            <Layout.Col span="7">
              <p className="text-medium-light">本轮累计</p>
            </Layout.Col>
            <Layout.Col className="align-right" span="17">
              <span className="title-small-light">{min2EOS(eos) || 0} EOS</span>
            </Layout.Col>
          </Layout.Row>
          <Layout.Row className="">
            <Layout.Col span="9">
              <p className="text-medium-light">本轮游戏分红</p>
            </Layout.Col>
            <Layout.Col className="align-right" span="15">
              <span className="title-small-light">{min2EOS(parseFloat(gameProfit.toFixed(4))) || 0} EOS</span>
            </Layout.Col>
          </Layout.Row>
          <p className="text-small-light-m tabs-basic-bottom-gap">= 本轮投入eos * (35%key分红 + 20%直推送)</p>

          <Layout.Row className="">
            <Layout.Col span="7">
              <p className="text-medium-light">我的分红</p>
            </Layout.Col>
            <Layout.Col className="align-right" span="17">
              <span className="title-small-light">{min2EOS(myProfit) || 0} EOS</span>
            </Layout.Col>
          </Layout.Row>
          <p className="text-small-light-m">= key分红+key分红锁+直推分红+直推分红锁+历史结余</p>
        </div>
      </div>,
      <div key="game-info-part-2" className="tabs-wrapper tabs-bottom-gap">
        <div className="tabs-game-card-content-wrapper">
          <Layout.Row className="tabs-row-round">
            <Layout.Col span="12">
              <p className="text-medium-light">历史累计</p>
            </Layout.Col>
            <Layout.Col className="align-right" span="12">
              <span className="title-small-light">{min2EOS(total) || 0} EOS</span>
            </Layout.Col>
          </Layout.Row>
          <Layout.Row className="">
            <Layout.Col span="12">
              <p className="text-medium-light">已分红</p>
            </Layout.Col>
            <Layout.Col className="align-right" span="12">
              <span className="title-small-light">{min2EOS(total * 0.675) || 0} EOS</span>
            </Layout.Col>
          </Layout.Row>
          <p className="text-small-light-m">= 35%(key分红) + 20%(直推) + 12.5%(奖池的一半)</p>
        </div>
      </div>,
      <Rank key="game-info-part-3" />
    ]
  }
}

function mapStateToProps(state) {
  return {
    language: state.language,
    roundInfo: state.roundInfo || {},
    playerInfo: state.playerInfo || {},
  };
}

function mapDispatchToProps(dispatch) {
  return bindActionCreators({}, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(GameInfo);
