/**
 * 输入key，换算eos
 **/
import BigNumber from 'bignumber.js';

export const key2EOS = (keyTotal, keyBuy) => {
  // EOS_Spend = (0.0002475 * Supply + 0.00012375 * Key_Buy + 100) * Key_Buy
  const val = new BigNumber(0.0002475).times(keyTotal);
  return new BigNumber(0.00012375).times(10).times(keyBuy).plus(val).plus(100).times(keyBuy).div(10000).toNumber()
};

