import React, { useState } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from "redux";
import { min2EOS } from '../../Utils/unitConvert';
import { updateModal } from '../../actions/modal';
import './Tabs.scss';
import i18n from '../../language';


const Rank = (props) => {
  const [isFold, setFold] = useState(false);

  const getRoundName = (rid) => {
    if (rid === props.round - 1) {
      return '上一轮大奖'
    }
    return `第${rid}轮大奖`
  };
  if (props.rankList.length === 0) {
    return null
  }

  return (
    <div className="tabs-wrapper">
      <div className="title-medium-light tabs-bottom-gap">获奖统计 <p className="el-icon-arrow-down align-right" /></div>
      <div className='tabs-game-card-content-wrapper tabs-bottom-gap'>
        {
          props.rankList.map((d, i) => {
            return (
              <div key={`rank-item-${i}`} className="rank-item-wrapper">
                <p className="text-medium-light tabs-small-bottom-gap">{getRoundName(d.rid)}</p>
                <p className="text-medium-light align-center tabs-small-bottom-gap">{d.winner}</p>
                <div className="title-medium-light align-center tabs-small-bottom-gap">{min2EOS(d.amount)} <span className=''>EOS</span></div>
                {
                  d.rank[0] &&
                  <p className="text-medium-light align-center tabs-small-bottom-gap">赚钱达人No.1</p>
                }
                {
                  d.rank[0] &&
                  <p className="text-medium-light align-center tabs-small-bottom-gap">{d.rank[0].first}</p>
                }
                {
                  d.rank[0] &&
                    <p
                      className="tabs-rank-label-top10"
                      onClick={() => {
                        props.updateModal({
                          open: true,
                          type: 'rank-list',
                          data: {index: i}})
                      }}
                    >top10</p>
                }
                { i !== props.rankList.length - 1 && <div className="line-light-m tabs-small-bottom-gap" /> }
              </div>
            )
          })
        }
      </div>
    </div>
  )
};

function mapStateToProps(state) {
  return {
    language: state.language,
    round: state.roundInfo.round,
    rankList: /*[{rid: '1', winner: 'testststt', amount: 123, rank: [{amount: 1, name: 'xxxx'}]}] || */state.rankInfo.rankList || [],
  };
}

function mapDispatchToProps(dispatch) {
  return bindActionCreators({updateModal}, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(Rank);
